














import Vue from "vue";
import Component from "vue-class-component";
import { State, Action, Getter } from "vuex-class";
import { WaterState, WaterStation } from "../store/water/types";
import excel from "../libs/excel";

interface Report {
  站名: string;
  站碼: string;
  行政區: string;
  即時水位: number;
  二級警戒值: number;
  一級警戒值: number;
  堤頂高: number;
  狀態: string;
  權責單位: string;
  資料時間: string;
}

@Component
export default class WaterReportPage extends Vue {
  @State("water") waterState: WaterState;
  get reportList() {
    let ret: Report[] = [];
    for (let station of this.waterState.stations) {
      let report: Report = {
        站名: station.Name,
        站碼: station.Name,
        行政區: station.District,
        即時水位: station.WaterLevel,
        二級警戒值: station.AlarmThreshold2,
        一級警戒值: station.AlarmThreshold1,
        堤頂高: station.LevelHeight,
        狀態: station.AlarmState,
        權責單位: station.Owner,
        資料時間: station.Timestamp
      };
      ret.push(report);
    }
    return ret;
  }

  exportLoading: boolean = false;

  exportExcel() {
    if (this.reportList.length) {
      this.exportLoading = true;
      const params = {
        title: [
          "站名",
          "站碼",
          "行政區",
          "即時水位",
          "二級警戒值",
          "一級警戒值",
          "堤頂高",
          "狀態",
          "權責單位",
          "資料時間"
        ],
        key: [
          "站名",
          "站碼",
          "行政區",
          "即時水位",
          "二級警戒值",
          "一級警戒值",
          "堤頂高",
          "狀態",
          "權責單位",
          "資料時間"
        ],
        data: this.reportList,
        autoWidth: true,
        filename: "水位即時報表"
      };
      excel.export_array_to_excel(params);
      this.exportLoading = false;
    } else {
      alert("無資料!");
    }
  }
}
